  .formInput {
    border-radius: 15px;
    background-color: #fff;
    border: 2px solid #341046;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
    color: #1E1E1F;
    font-size: 20px;
    text-align: center;
    padding: 12px;
    /* height: 96px; */
    width: calc(100% - 25px);
  }
  
  .formInput::placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
  }
  
  .TodoForm-buttonContainer {
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
  
  .TodoForm-button {
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: #202329;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 2px;
    border: none;
    font-family: 'Montserrat';
    border-radius: 15px;

  }
  
  .TodoForm-button--add {
    background: #d55303;
    color: white;
    /* box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5); */
  }
  .TodoForm-button--add:hover {
    background: #ab4302;
  }
  
  .TodoForm-button--cancel {
    color: white;
    background: #4b1765;
  }
  .TodoForm-button--cancel:hover {
    background: #341046;
  }