.TodoSearch {
  background: #dadada;
  border: 2px solid #202329;
  border-radius: 20px;
  height: 35px;
  width: 80%;

  font-size: 15px;
  text-align: center;
  font-family: 'Montserrat';
  font-weight: 400;
  color: #1E1E1F;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
  position: relative;
  display: block;
  margin : 0 auto;
}

.TodoSearch::placeholder {
  color: #A5A5A5;
  font-family: 'Montserrat';
  font-weight: 400;
}

.TodoSearch:focus {
  outline-color: #341046;
}